* {
    text-align: center;
}

.swiper-slide {
    width: 100%;
    background-color: #fff;
}

.swiper-pagination {
    text-align: right;
    padding-right: 15px;
}

.header {
    display: block;
    text-align: center;
}

ul {
    display: block;
    text-align: center;
    margin-left: 0;
    margin-right: 0;
}

.nav-pills li {
    width: 32%;
    padding: 0!important;
    margin: 0;
}

.nav-pills li a {
    padding: 4px 0 4px 0;
}